.tile:hover {
    transform: scale(1.05);
}

.tile {
    transition: transform .2s ease;
}

.tileInner {
    height: 17em;
    width: 325px;
    margin: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #edeef1;
    cursor: pointer;
}

.tileTitle {
    font-weight: bold;
    font-size: 17px;
    margin-top: 1px;
    color: #6a7079;
}

.tileDescription {
    font-size: 13px;
    margin: 10px;
    bottom: 0;
    color: #3c4046;
}

.tileImage {
    height: 73%;
    width: 100%;
    background-size: cover;
    background-position: center;
    margin: 13px;
    margin-top: 0;
}

.popup-enter {
    opacity: 0;
    transform: scale(0.9);
}

.popup-enter-active {
    opacity: 1;
    transform: translateX(0);
    transition: opacity 300ms, transform 300ms;
}
.popup-exit {
    opacity: 1;
}
.popup-exit-active {
    opacity: 0;
    transform: scale(0.9);
    transition: opacity 300ms, transform 300ms;
}

