h2 > a {
    position: relative;
    color: #000;
    text-decoration: none;
}
  
h2 > a:hover {
color: #000;
}

h2 > a::before {
content: "";
position: absolute;
width: 100%;
height: 2px;
bottom: 0;
left: 0;
background-color: #000;
visibility: hidden;
transform: scaleX(0);
transition: all 0.3s ease-in-out 0s;
visibility: visible;
transform: scaleX(1);
}

h2 > a:hover::before {
visibility: visible;

transform: scaleX(1);
}

.projectLink {
    text-decoration: none;
}

@media only screen and (max-width: 812px) {
